.App {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  flex-direction: column;
}

.Wrapper {
  width: 100%;
  border-radius: 20px;
  max-width: 700px;
  height: 600px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Wrapper .Header {
  width: 100%;
  max-width: 550px;
  padding: 20px;
}

.Wrapper .Header h1 {
  font-size: 45px;
  text-align: center;
  padding: 10px;
}

.Wrapper .Form {
  display: flex;
  justify-content: center;
}

.Wrapper .Form input {
  width: 80%;
  font-size: 22px;
  padding: 10px 15px;
  padding-left: 15px;
  border-radius: 10px 0px 0px 10px;
  border: none;
  padding: 14px 32px 14px 16px;
  border: 2px solid #5d0cff;
  outline: none;
  background: transparent;
  color: #fff;
}

.Wrapper .Form button {
  border: none;
  width: 80px;
  color: #fff;
  border-radius: 0px 10px 10px 0px;
  font-size: 22px;
  transition: 0.2s all;
  background: linear-gradient(
    90deg,
    rgba(93, 12, 255, 1) 0%,
    rgba(155, 0, 250, 1) 100%
  );
}

.Wrapper .Form button:hover {
  cursor: pointer;
}

.Wrapper .Todo {
  width: 100%;
  max-width: 550px;
}

.Wrapper .Todo .TodoItem {
  display: flex;

  color: #fff;
  background: linear-gradient(
    90deg,
    rgba(93, 12, 255, 1) 0%,
    rgba(155, 0, 250, 1) 100%
  );
  margin: 5px;
  padding: 16px;
  border-radius: 5px;
}

.Wrapper .Todo .TodoItem p {
  display: flex;
  width: 100%;
  font-size: 22px;
}

.Wrapper .Todo .TodoItem div {
  font-size: 22px;
  transition: 0.4s all;
}

.Wrapper .Todo .TodoItem div:hover {
  color: red;
  cursor: pointer;
}

.Direct {
  /* background-color: #292728; */
  width: 100%;
  position: fixed;
  top: 0;
  padding: 10px;
}

ul {
  display: flex;
  flex-direction: row-reverse;
}

li {
  list-style: none;
  border: 2px solid rgba(93, 12, 255, 1);
  text-align: center;
  padding: 10px;
  margin: 4px;
  border-radius: 10px;
  transition: 0.5s all;
}

li:hover {
  border-radius: 30px;
}

a {
  text-decoration: none;
  font-weight: bold;
  color: rgba(93, 12, 255, 1);
}

